img{width:100%;height:100%}.gameOver{background-color:red}.remove{display:none}.apply-shake{animation:shake 0.5s cubic-bezier(0.36, 0.07, 0.19, 0.97) both}@keyframes shake{10%,90%{transform:translate3d(-1px, 0, 0)}20%,80%{transform:translate3d(2px, 0, 0)}30%,50%,70%{transform:translate3d(-4px, 0, 0)}40%,60%{transform:translate3d(4px, 0, 0)}}.main-container{background-image:url(img/bg.f01e8.jpg)}.menu-container{display:flex;justify-content:space-evenly;width:600px;height:100px;margin:auto}.controller{display:flex;justify-content:space-between;align-items:center;flex-direction:column}.start-container{align-self:center}.start{background-color:red;border-radius:8px;border:0;display:inline-block;cursor:pointer;color:#ffffff;font-size:18px;padding:8px 25px}.timer{display:flex;align-items:center;flex-direction:column}.arrow{background-color:transparent;border:solid teal;border-width:0 3px 3px 0;display:inline-block;padding:3px;height:16px;width:16px}.arrow:hover{border:solid red;border-width:0 3px 3px 0;animation:none;cursor:pointer}.up{transform:rotate(-135deg);-webkit-transform:rotate(-135deg)}.down{transform:rotate(45deg);-webkit-transform:rotate(45deg)}.blink{animation:blinker 2.5s linear infinite}@keyframes blinker{50%{opacity:0.3}}.field-container{display:block;margin:auto;width:800px;margin-top:10px}.row{display:flex;justify-content:center}.cells{width:100px;height:100px;margin:10px;border:0}.cells:hover{background-color:white}
